#code-id {
    background-image: url('../images/coffee-mac.jpg');
    background-size: cover;
    background-attachment: fixed;
    padding: 2rem 0 5rem 0;
    overflow: hidden;
}
.code ul li a {
    color: lightblue;
}
.code ul li a:visited {
    color: lightgrey;
    text-decoration: none;
}

.hero.code {
    display: flex;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.6);
    max-width: 1024px;
    min-width: 90%;
    margin: auto;
    justify-content: center;
    color: white;
    position: relative;
    /* background-image: linear-gradient(to top, #ffffff 0%, rgb(232, 232, 232) 20%, rgb(232, 232, 232) 70%, #ffffff 100%);  */
    /* background-image: linear-gradient( -75deg,#23afba 0%,#298dbf 50%, grey 100% ); */
}
.code .section-icon {
    position: absolute;
    top: -1.5rem;
    padding: 1rem 1.3rem;
    background-color: black;
    font-size: 2rem;
    border-radius: 4rem;
    right: -1.5rem;
}


.code h1, h2 {
    text-align: center;
    width: 100%; 
    padding: 2rem 0;
}

